	return (
		<Link href="/">
			<StyledLogo>
				<Image src={pc_logo} alt="logo" width={ 100 } height={ 36 } className="desktop-logo" priority />
				{/*<Image src={mo_logo} alt="logo" width={36} height={36} fill={'red'} className="mobile-logo" priority />*/}
			</StyledLogo>
		</Link>
	);
